import React, { useState, useEffect } from 'react';
import { NavLink } from 'react-router-dom';
import classNames from 'classnames';
import { dynamicAdapt } from '../../js/dynamicAdapt';
import './Header.scss';

export const Header: React.FC = () => {
  const [menuOpen, setMenuOpen] = useState(false);
  const screenWidth = window.screen.width;

  dynamicAdapt();

  useEffect(() => {
    dynamicAdapt();
  }, [screenWidth]);

  return (
    <div className="Header" id="header">
      <div className="Header__logo"></div>

      <nav
        className={
          classNames(`Header__nav + ${menuOpen
            ? 'Header__nav--open'
            : ''
          }`)
        }
      >
        <NavLink
          to="/home"
          className="Header__link"
        >
          Home
        </NavLink>
        <NavLink
          to="/explore"
          className="Header__link"
        >
          Explore
        </NavLink>
        <NavLink
          to="/travel"
          className="Header__link"
        >
          Travel
        </NavLink>
        <NavLink
          to="/blog"
          className="Header__link"
        >
          Blog
        </NavLink>
        <NavLink
          to="/pricing"
          className="Header__link"
        >
          Pricing
        </NavLink>
      </nav>

      <div className="Header__actions">
        <NavLink
          to="/login"
          className="Header__link Header__link--login"
          data-da=".Header__nav--open, 510, first"
        >
          Login
        </NavLink>
        <button
          type="button"
          className="Header__signUp"
          data-da=".Header__nav--open, 770, last"
        >
          <NavLink
            to="/signUp"
            className="Header__link Header__link--sign-up"
          >
            Sign up
          </NavLink>
        </button>

        <button
          className={
            classNames(`Header__hamburger + ${menuOpen
              ? 'Header__hamburger--open'
              : ''
            }`)
          }
          type="button"
          onClick={() => setMenuOpen(!menuOpen)}
        >
          .
        </button>
      </div>
    </div>
  );
};
